<template>
    <div class="state-info">
        <span class="state-name">{{ state.name }}</span>
        <div>
            <input class="visit-state" type="checkbox" v-model="visited">
        </div>

        <div>
            <router-link v-bind:to="{ name: 'StateMap', params: { state: state.name } }">
                <img class="map-icon" src="@/assets/map_icon.png">
            </router-link>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'StateDetail',
        emits: ['update-visited'],
        props: {
            state: Object
        },
        data() {
            return {
                visited: this.state.visited // copies from prop
            }
        },
        watch: {
            visited() {
                this.$emit('update-visited', this.state.name, this.visited)
            }
        }
    }
</script>

<style scoped>

    .state-info {
        padding: 1rem;
        height: 8 rem;
        width: 10rem;
        border: 1px gainsboro solid;
        border-radius: 0.3rem;
        background-color: whitesmoke;
    }

    .visit-state {
        margin: 1rem;
    }
</style>